import Print from './printarea.js';
/**
 * @file 
 * `v-print`,
 * `v-print="'#id'"` , .
 */
export default {
	directiveName: 'print',
	bind(el, binding, vnode) {
		let vue = vnode.context;
		let closeBtn = true;
		let id = '';
		el.addEventListener('click', () => {
			vue.$nextTick(() => {
				if (typeof binding.value === 'string') {
					id = binding.value;
				} else if (typeof binding.value === 'object' && !!binding.value.id) {
					id = binding.value.id;
					let ids = id.replace(new RegExp("#", "g"), '');
					let elsdom = document.getElementById(ids);
					if (!elsdom) console.log("id in Error"), id = '';
				}
				// 
				if (id) {
					localPrint();
				} else {
					// 
					window.print();
				}
				// if(binding.value.mode){

				// }
			});

		});
		const localPrint = () => {
			if (closeBtn) {
				closeBtn = false;
				new Print({
					ids: id, // * id
					standard: '', // html5 html5loosestrict
					extraHead: binding.value.extraHead, // head,
					extraCss: binding.value.extraCss, // css
					popTitle: binding.value.popTitle, // title
					mode:binding.value.mode?1:0,//
					callback(){
						if(typeof binding.value.callback ==='function'){
							binding.value.callback()
						}
					},
					endCallback() { // 
						closeBtn = true;
						if(typeof binding.value.endCallback ==='function'){
							binding.value.endCallback()

						}
					}
				});
			}
		};
	}
};